const app = getApp().globalData;
const { navBar } = app;
export default {
	data() {
		return { navBar: navBar };
	},
	methods:{
		leftClick(){
			uni.navigateBack({
				delta:1
			})
		},
		recordPage(){
			uni.navigateTo({
				url:'./investlist'
			})
		}
	}
};
